.shoot {
  font: 14px/1.5 'papyrus', sans-serif;
  background-color: #232323;
}

.sidebar {
  position: fixed;
  top: 0;
  left: 20px;
  width: 250px;
  text-align: right;
}

.sidebar a {
  display: inline-block;
  background-color: #fff;
  margin-bottom: 5px;
  padding: 0 2px;
  color: #000;
  font-size: 14px;
  text-decoration: none;
}

.title {
  padding: 0 2px;
  font-size: 16px;
  font-style: italic;
  font-family: 'Adobe Hebrew', 'Tahoma', serif;
  font-weight: normal;
  display: inline-block;
  background-color: #fff;
  color: #000;
}

.content {
  margin-left: 250px;
  float: left;
}

.item {
  width: 800px;
  margin-bottom: 5px;
}

.content img {
  display: block;
  width: 100%;
}

.unite {
  overflow: hidden;
}

.unite img {
  width: 50%;
  display: block;
  float: left;
}

.phone img {
  width: 600px;
}

.tip {
  margin: 10px 0;
  font-size: 12px;
  color: var(--text-color-light);
}
@media screen and (max-width: 600px) {
  .sidebar {
    right: 0;
    left: inherit;
  }

  .content {
    margin: 0 5px;
  }

  .content .item {
    width: 100%;
    max-width: 600px;
  }

  .phone img {
    width: 100%;
  }
}
